@use 'sass:color';
@use 'base/variables' as v;
@use 'base/mixins' as m;

.video{
    // siempre el padre tiene que ser relativo
    position: relative;  
    min-height: 50rem;
    max-height: 70rem;
    overflow: hidden;
    .overlay{
        position: absolute;
        background: linear-gradient(
            270deg,
            rgba(v.$morado, 60%),
            rgba(color.adjust(v.$morado, $lightness: -60%), 0.9)


        );
        inset: 0;
        display: flex;
        align-items: center;
    }
    video{
       display: block;
       width: auto;
       @include m.desktop{
            width: 100%;
       }
    }
}
.contenido-video{
    //orden de forma vertical
    //puedes controlar que elementos van detras de otro
    z-index: 10;
    h2{
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;  
    }
    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-size: 2.4rem;
        font-weight: bold;
        margin: 0;
    }
}
