@use 'variables' as v;


@mixin telefono{
    @media (min-width:v.$telefono) {
       @content;
    }
}
@mixin tablet{
    @media (min-width:v.$tablet) {
       @content;
    }
}
@mixin desktop{
    @media (min-width:v.$desktop) {
       @content;
    }
}
@mixin desktopXL{
    @media (min-width:v.$desktopXL) {
       @content;
    }
}
@mixin contenedor{
        width: 95%;
        max-width: 120rem;
        margin: 0 auto;
}
//pasar parametros a unos mixins
//podemos poner valores prestablecidos
@mixin grid ($columnas: 1, $gap: 2rem){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $gap;
}
@mixin resetear-lista{
    margin: 0;
    padding: 0;
    list-style: none;
}